const FormFooter = styled.div`

`

class Form extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      signMethod: null,
      signName: null,
      trimSignPadURL: null,
      signPadComplete: false,
    }
  }

  // Defining signature pad object
  signPadCanvas = {}

  // Set sign method state
  setSignMethod(state) {
    if (this.state.signPadComplete) {
      this.setState({
        signMethod: state,
        trimSignPadURL: this.state.trimSignPadURL ? this.state.trimSignPadURL : this.signPadCanvas,
      }, () => {
        this.updateSignURL()
      })
    } else {
      this.setState({
        signMethod: state,
      })
    }
  }

  setSignPadComplete(state) {
    this.setState({
      signPadComplete: state,
      trimSignPadURL: this.signPadCanvas,
    })
  }

  // Set sign name state
  setSignName(state) {
    this.setState({
      signName: state,
    })
  }

  // Clear signature pad
  clear = () => {
    this.setState(
      {
        trimSignPadURL: null,
        signPadComplete: false,
      }, () => {
        this.signPadCanvas.clear()
      }
    )
  }

  // Form submit
  submitForm = () => {
    if (this.state.signMethod === 'draw') {
      this.setState(
        {
          trimSignPadURL: this.signPadCanvas.getTrimmedCanvas().toDataURL('image/png')
        }, () => {
          this.updateSignURL()
          this.closePopup()
        }
      )
    } else {
      this.generateSignPreview()
    }
  }

  generateSignPreview() {
    let _this = this

    htmlToImage.toPng(document.querySelector('#generated-sign-png'))
      .then(function (dataUrl) {
        var img = new Image();
        img.src = dataUrl;

        _this.setState({
          trimSignPadURL: img.src,
        }, () => {
          _this.updateSignURL()
          _this.closePopup()
        },)
      })
      .catch(function (error) {
        console.error('oops, something went wrong!', error);
    });

  }

  updateSignURL() {
    this.props.getSignURL(
      this.state.trimSignPadURL,
      this.state.signName,
    )
  }

  closePopup() {
    this.props.onClose()
  }

  componentDidUpdate() {
    if (this.state.trimSignPadURL && this.signPadCanvas && typeof this.state.trimSignPadURL === 'object') {
      this.signPadCanvas.fromData(this.state.trimSignPadURL.toData())
    }
  }

  render () {
    const { onClose } = this.props
    const { signMethod, signName, signPadComplete, trimSignPadURL } = this.state

    return (
      <PopUpContainer className="form-modal">
        <ButtonClose type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={onClose}>
          <FontAwesomeIcon icon={faTimes} />
        </ButtonClose>

        <FormContainer>
          <form action="">
            <LeftPane>
              <H3>Electronic signature</H3>
              <FormGroup className="form-field field-method">
                <FormGroupLabel>Select method <FormGroupLabelAsterik className="required">*</FormGroupLabelAsterik></FormGroupLabel>
                <ButtonnGroupContainer>
                  <ButtonDefault id="btn-draw" className={(signMethod && signMethod === 'draw') ? 'selected' : null} type="button" onClick={() => this.setSignMethod('draw')}>Draw</ButtonDefault>
                  <ButtonDefault id="btn-generate" type="button" className={(signMethod && signMethod === 'generate') ? 'selected' : null} onClick={() => this.setSignMethod('generate')}>Generate</ButtonDefault>
                </ButtonnGroupContainer>
                {signMethod && (
                  <FormValidationIcon show={true} success={true}><FontAwesomeIcon icon={faCheck} /></FormValidationIcon>
                )}
              </FormGroup>
              <FormGroup disabled={signMethod ? false : true} className="form-field field-name">
                <FormGroupLabel>Confirm your name for signature <FormGroupLabelAsterik className="required">*</FormGroupLabelAsterik></FormGroupLabel>
                <FormGroupInput className={`input-field ${signName && signName.length > 2 ? 'complete' : ''}`} name="signName" placeholder="Your name or intials here" maxLength="25" onChange={(e) => this.setSignName(e.target.value)} />
                {signName && signName.length > 2 && (
                  <FormValidationIcon show={true} success={true}><FontAwesomeIcon icon={faCheck} /></FormValidationIcon>
                )}
              </FormGroup>
            </LeftPane>

            <RightPane>
              <FormGroup disabled={(signName && signName.length > 2) ? false : true} className="form-field field-signature-pad">
                {signMethod === 'draw' ? (
                  <>
                    <FormGroupLabel>Draw your signature <FormGroupLabelAsterik className="required">*</FormGroupLabelAsterik></FormGroupLabel>
                    <PreviewOptions className={`${signPadComplete ? 'show' : 'hide'}`}>
                      <ButtonPencil><FontAwesomeIcon icon={faPencil} /></ButtonPencil>
                      <ButtonClean id="btn-clear" type="button" onClick={this.clear}><FontAwesomeIcon icon={faTrashAlt} /> <span>Delete</span></ButtonClean>
                    </PreviewOptions>
                    <GeneratedDrawCanvas>
                      <SignatureCanvas
                        ref={(ref) => { this.signPadCanvas = ref }}
                        onEnd={() => this.setSignPadComplete(true)}
                        penColor="#012F57"
                        clearOnResize={false}
                      />
                    </GeneratedDrawCanvas>
                  </>
                ) : (
                  <>
                    <FormGroupLabel>Signature preview <FormGroupLabelAsterik className="required">*</FormGroupLabelAsterik></FormGroupLabel>
                    <GeneratedSign><div id="generated-sign-png">{(signName && signName.length > 2) ? signName : null}</div></GeneratedSign>
                  </>
                )}
              </FormGroup>
              <FormFooter>
                <ButtonSubmit disabled={
                  ((signMethod && signMethod === 'draw' && signPadComplete) || (signMethod && signMethod === 'generate' && signName && signName.length > 2)) ? false : true
                  } id="btn-submit" type="button" onClick={this.submitForm}>Adopt &amp; sign</ButtonSubmit>
                <ButtonCancel id="btn-cancel" type="button" onClick={onClose}>Cancel</ButtonCancel>
              </FormFooter>
            </RightPane>
          </form>
        </FormContainer>
      </PopUpContainer>
    )
  }
}

export default Form
