import React, { useState, useEffect } from 'react'
import styled from 'styled-components'

import CheckboxButtonGroup, { DIFFERENT_BILLING_ADDRESS_VALUE } from '../CheckboxButtonGroup'
import RadioButtonGroup from '../RadioButtonGroup'
import BankAccountTab from '../BankAccountTab'
import CreditCardPaymentTab from '../CreditCardPayment'
import PayDeductionTab from '../PayDeductionTab'
import BillingAddressFields from '../BillingAddress/BillingAddressFields'
import ProgressButton, { ButtonStates } from '../ProgressButton'
import H3 from '../H3'
import ProgressButtonPositioningDiv from '../ProgressButton/ProgressButtonPositioningDiv'

import { STATE_FROM_SUBURB, EMPLOYER_SELECTED_OR_TYPED } from '../FormHighOrder/types/YourWorkField'
import { EMPLOYMENT_STATUS } from '../FormHighOrder/types/AboutYouField'

import { useSelector, useDispatch } from 'react-redux'
import { RootState } from 'store/'
import {
  writeYourContributionData,
  writeForm4BillingAddressInfo,
  writeForm4BankAccountTabData,
  initialState,
  writeForm4CreditCardTabData,
  writeForm4PayDeductionTabData,
  writeForm4PaymentMethodValidated
} from '../FormHighOrder/features/index'

import {
  CREDITCARD_PAYMENT_TAB_DATA,
  BANKACCOUNT_PAYMENT_TAB_DATA,
  PAY_DEDUCTION_PAYMENT_TAB_DATA,
  BILLING_ADDRESS_OPTION,
  DIFFERENT_BILLING_ADDRESS_DATA,
  PAYMENT_METHOD_SELECTION, PAY_DEDUCTION_PAYMENT_ELOTRONIC_SIGNATURE,
  CREDITCARD_PAYMENT_AGREE_TERM_AND_CONDITION, BANKACCOUNT_AGREE_TERM_AND_CONDITION
} from '../FormHighOrder/types/YourContributionField'

export type Props = {
  onFormValidatedProp?: (isValidated: boolean) => void,
  onHandleFormValidated?: (isValidate: boolean) => void
}

const FormStep4Wrapper = styled.div`
  position: relative;
  animation-name: flickity;
  animation-duration: 1.5s;  
  animation-fill-mode: forwards;
  @keyframes flickity {
    0% {
        opacity:0;
    }
    100% {
        opacity:1;
    }
  };
  @media (max-width: 1024px) {
    margin:auto;
  };
  @media screen and (max-width: 768px) {
    width: 100%;
    max-width: 100%;
  }
`

const PositioningRadioButtonGroup = styled(RadioButtonGroup)`
  margin-top: 2.5rem;
`

const FormStepFour: React.FC<Props> = ({ onFormValidatedProp, onHandleFormValidated }) => {
  const dispatch = useDispatch()

  const [paymentTab, setPaymentTab] = useState(-1)

  // Three states bellow are represented for:  
  // 1. If user select on billing address field; 
  // 2. billingAddressOption = home or different;
  // 3. if billingAddressOption === different, are those input validated?    
  const [isBillingAddressSelected, setIsBillingAddressSelected] = useState(false)
  const [billingAddressOption, setBillingAddressOption] = useState('')
  const [billingAddressValidation, setBillingAddressValidation] = useState(false)

  const [billingAddressData, setBillingAddressData] = useState<any>(undefined)
  const [bankAccountTabData, setBankAccountTabData] = useState<any>(undefined)
  const [creditCardTabData, setCreditCardTabData] = useState<any>(undefined)
  const [payDeductionTabData, setDeductionTabData] = useState<any>(undefined)

  // Payment method selection
  const [paymentMethodSelection, setPaymentMethodSelection] = useState('')
  const [creditCardPaymentValidation, setCreditCardPaymentValidation] = useState(false)
  const [bankAccountPaymentValidation, setBankAccountPaymentValidation] = useState(false)
  const [payDeductionPaymentValidation, setPayDeductionPaymentValidation] = useState(false)

  const [proceedingButtonState, setProceedingButtonState] = useState(ButtonStates.disabled)
  const [progressButtonTilte, setProgressButtonTilte] = useState('Finish & submit')

  const [payrollDeductionTitle, setPayrollDeductionTitle] = useState('')
  const [isPayrollDeductionAvailable, setIsPayrollDeductionAvailable] = useState(true)

  const isPaymentMethodSelectionValidated = (data: any) =>
    data[CREDITCARD_PAYMENT_TAB_DATA].isValidated
    || data[BANKACCOUNT_PAYMENT_TAB_DATA].isValidated
    || data[PAY_DEDUCTION_PAYMENT_TAB_DATA].isValidated

  const isDifferentBillingAddressSelectionValidated = (data: any) =>
    // data[DIFFERENT_BILLING_ADDRESS_DATA].isValidated
    true

  const getValidate = useSelector((state: RootState) => {
    const { data } = state.FormHighOrder.pageData[3]
    if (billingAddressOption === DIFFERENT_BILLING_ADDRESS_VALUE) {
      return isPaymentMethodSelectionValidated(data) && isDifferentBillingAddressSelectionValidated(data)
    }
    else return isPaymentMethodSelectionValidated(data)
  })

  const getPrefetchData = useSelector((state: RootState) =>
    state.FormHighOrder.pageData[3].data)

  const getStateFromForm1 = useSelector((state: RootState) =>
    state.FormHighOrder.pageData[0].data[STATE_FROM_SUBURB].value)

  const getEmployerSelectedOrTypedFromForm1 = useSelector((state: RootState) =>
    state.FormHighOrder.pageData[0].data[EMPLOYER_SELECTED_OR_TYPED].value)

  const getStateFromForm2 = useSelector((state: RootState) =>
    state.FormHighOrder.pageData[1].data[EMPLOYMENT_STATUS].value)

  useEffect(() => {
    if (getValidate && paymentMethodSelection === 'Bank account' &&
      (bankAccountPaymentValidation && isBillingAddressSelected)) {
      setProceedingButtonState(ButtonStates.enabled)
    }
    else if (getValidate && paymentMethodSelection === 'Credit card' &&
      (creditCardPaymentValidation && isBillingAddressSelected)) {
      setProceedingButtonState(ButtonStates.enabled)
    }
    else if (getValidate && paymentMethodSelection === 'Payroll deduction' &&
      (payDeductionPaymentValidation && isBillingAddressSelected)) {
      setProceedingButtonState(ButtonStates.enabled)
    }
    else setProceedingButtonState(ButtonStates.disabled)
  }, [
    getValidate, paymentMethodSelection,
    bankAccountPaymentValidation,
    creditCardPaymentValidation,
    payDeductionPaymentValidation,
    isBillingAddressSelected
  ])

  useEffect(() => {
    if (billingAddressValidation === true) {
      setValidationField(BILLING_ADDRESS_OPTION, isBillingAddressSelected)
      getValidate
        && (billingAddressValidation && isBillingAddressSelected) ?
        setProceedingButtonState(ButtonStates.enabled) : setProceedingButtonState(ButtonStates.disabled)
    }
  }, [
    getValidate,
    billingAddressOption,
    isBillingAddressSelected
  ])

  useEffect(() => {
    if (getPrefetchData[BILLING_ADDRESS_OPTION].value !== undefined) {
      setBillingAddressOption(getPrefetchData[BILLING_ADDRESS_OPTION].value)
      setIsBillingAddressSelected(getPrefetchData[BILLING_ADDRESS_OPTION].isValidated)
    }
    if (getPrefetchData[PAYMENT_METHOD_SELECTION].value !== undefined) {
      setPaymentMethodSelection(getPrefetchData[PAYMENT_METHOD_SELECTION].value)
    }
  }, [getPrefetchData[BILLING_ADDRESS_OPTION].value, getPrefetchData[PAYMENT_METHOD_SELECTION].value])

  useEffect(() => {
    if (billingAddressOption !== '') {
      setField(BILLING_ADDRESS_OPTION, billingAddressOption)
    }
    if (paymentMethodSelection !== '') {
      setField(PAYMENT_METHOD_SELECTION, paymentMethodSelection)
    }
    if (creditCardPaymentValidation !== false) {
      dispatchCreditCardTabValidated(CREDITCARD_PAYMENT_TAB_DATA)
    }
    if (bankAccountPaymentValidation !== false) {
      dispatchBankAccountTabValidated(BANKACCOUNT_PAYMENT_TAB_DATA)
    }
    if (payDeductionPaymentValidation !== false) {
      dispatchPayDeductionTabValidated(PAY_DEDUCTION_PAYMENT_TAB_DATA)
    }
    if (billingAddressValidation === true && billingAddressData !== undefined) {
      const valueData = initialState.pageData[3].data[DIFFERENT_BILLING_ADDRESS_DATA]
      for (let nameParams in valueData) {
        dispatchDifferentBillingAddress(nameParams, billingAddressData[nameParams].value)
      }
    }
    if (bankAccountTabData !== undefined && bankAccountPaymentValidation === true) {
      const valueData = initialState.pageData[3].data[BANKACCOUNT_PAYMENT_TAB_DATA].value
      for (let nameParams in valueData) {
        dispatchBankAccountData(nameParams, bankAccountTabData[nameParams].value)
      }
    }

    if (creditCardTabData !== undefined && creditCardPaymentValidation === true) {
      const valueData = initialState.pageData[3].data[CREDITCARD_PAYMENT_TAB_DATA].value
      for (let nameParams in valueData) {
        dispatchCreditCardData(nameParams, creditCardTabData[nameParams].value)
      }
    }

    if (payDeductionTabData !== undefined && payDeductionPaymentValidation === true) {
      const valueData = initialState.pageData[3].data[PAY_DEDUCTION_PAYMENT_TAB_DATA].value
      for (let nameParams in valueData) {
        dispatchPayDeductionTabData(nameParams, payDeductionTabData[nameParams].value)
      }
    }
  }, [billingAddressOption, paymentMethodSelection, billingAddressValidation,
    creditCardPaymentValidation, bankAccountPaymentValidation, payDeductionPaymentValidation,
    bankAccountTabData, creditCardTabData, payDeductionTabData, billingAddressData
  ])

  useEffect(() => {
    if ((getStateFromForm1 === 'SA' || getStateFromForm1 === 'NT'
      || getStateFromForm1 === 'Broken Hill')
      && getPrefetchData[PAYMENT_METHOD_SELECTION]?.value === undefined) {
      setPaymentMethodSelection('Credit card')
    }
    if ((getStateFromForm1 === 'WA' || getStateFromForm1 === 'Newcastle')
      && getPrefetchData[PAYMENT_METHOD_SELECTION]?.value === undefined) {
      setPaymentMethodSelection('Bank account')
    }
    if ((getStateFromForm1 === 'QLD' || getStateFromForm1 === 'TAS')) {
      setPayrollDeductionTitle('Payroll deduction')
      if (getPrefetchData[PAYMENT_METHOD_SELECTION]?.value === undefined
        || getPrefetchData[PAYMENT_METHOD_SELECTION]?.value === 'Payroll deduction') {
        setPaymentMethodSelection('Payroll deduction')
      }
      setIsPayrollDeductionAvailable(true)
      if (getStateFromForm2 === 'Salaried' || getEmployerSelectedOrTypedFromForm1 === 'typed') {
        setIsPayrollDeductionAvailable(false)
        setPaymentMethodSelection('')
      }
    }
  }, [getStateFromForm1, getStateFromForm2,
    getEmployerSelectedOrTypedFromForm1])

  useEffect(() => {
    if(getPrefetchData[CREDITCARD_PAYMENT_TAB_DATA].isValidated === true  
      && paymentMethodSelection === 'Credit card') {
      setPaymentTab(1)
    }
    if(getPrefetchData[BANKACCOUNT_PAYMENT_TAB_DATA].isValidated === true  
      && paymentMethodSelection === 'Bank account') {
      setPaymentTab(0)
    }
    if(getPrefetchData[PAY_DEDUCTION_PAYMENT_TAB_DATA].isValidated === true  
      && paymentMethodSelection === 'Payroll deduction') {
      setPaymentTab(2)
    }
  }, [getPrefetchData[CREDITCARD_PAYMENT_TAB_DATA].isValidated,
  getPrefetchData[BANKACCOUNT_PAYMENT_TAB_DATA].isValidated,
  getPrefetchData[PAY_DEDUCTION_PAYMENT_TAB_DATA].isValidated, 
  paymentMethodSelection])

  useEffect(() => {
    if (getValidate === true) {
      if (proceedingButtonState === "disabled") {
        onHandleFormValidated ? onHandleFormValidated(false) : {}
      } else {
        onHandleFormValidated ? onHandleFormValidated(true) : {}
      }
    }
  }), [
      proceedingButtonState
    ]

  const setValidationField = (nameParam: string, wasAnInputSuccess: boolean) => {
    dispatch(writeYourContributionData({
      key: nameParam,
      value: checkFetchData(nameParam) ? getPrefetchData[nameParam]?.value : undefined,
      isValidated: wasAnInputSuccess
    }
    ))
  }

  const setField = (nameParam: string, inputValue?: string) => {
    dispatch(writeYourContributionData({
      key: nameParam,
      value: inputValue ? inputValue : "",
      isValidated: true
    }
    ))
  }

  const dispatchDifferentBillingAddress = (nameParams: string, data: string) => {
    dispatch(writeForm4BillingAddressInfo({
      key: nameParams,
      value: data ? data : "",
      isValidated: true
    }
    ))
  }

  const dispatchBankAccountData = (nameParams: string, data: string) => {
    dispatch(writeForm4BankAccountTabData({
      key: nameParams,
      value: data ? data : "",
      isValidated: true
    }
    ))
  }

  const dispatchBankAccountTabValidated = (nameParams: string, data?: string) => {
    dispatch(writeForm4PaymentMethodValidated({
      key: nameParams,
      value: data ? data : "",
      isValidated: bankAccountPaymentValidation
    }
    ))
  }

  const dispatchCreditCardData = (nameParams: string, data: string) => {
    dispatch(writeForm4CreditCardTabData({
      key: nameParams,
      value: data ? data : "",
      isValidated: true
    }
    ))
  }
  const dispatchCreditCardTabValidated = (nameParams: string, data?: string) => {
    dispatch(writeForm4PaymentMethodValidated({
      key: nameParams,
      value: data ? data : "",
      isValidated: creditCardPaymentValidation
    }
    ))
  }

  const dispatchPayDeductionTabData = (nameParams: string, data: string) => {
    dispatch(writeForm4PayDeductionTabData({
      key: nameParams,
      value: data ? data : "",
      isValidated: true
    }
    ))
  }

  const dispatchPayDeductionTabValidated = (nameParams: string, data?: string) => {
    dispatch(writeForm4PaymentMethodValidated({
      key: nameParams,
      value: data ? data : "",
      isValidated: payDeductionPaymentValidation
    }
    ))
  }

  const checkFetchData = (nameParam: string) => {
    return getPrefetchData !== undefined && getPrefetchData[nameParam]?.value !== ""
  }

  return (
    <FormStep4Wrapper>
      <H3> Your contribution </H3>
      <CheckboxButtonGroup
        onSelectedLabel={selectedLabel => {
          setBillingAddressOption(selectedLabel)
          setIsBillingAddressSelected(true)
        }}
        inputFromProps={
          checkFetchData(BILLING_ADDRESS_OPTION) ?
            getPrefetchData[BILLING_ADDRESS_OPTION]?.value : null
        }
      />

      {
        (billingAddressOption === DIFFERENT_BILLING_ADDRESS_VALUE) &&
        <BillingAddressFields
          onBillingAddressDataValidated={wasSuccessful => {
            setBillingAddressValidation(wasSuccessful)
          }}
          onDifferentBillingAddressData={data => {
            setBillingAddressData(data)
          }}
        />
      }

      <PositioningRadioButtonGroup
        onSelectedValue={selectedValue => {
          setPaymentMethodSelection(selectedValue)
        }}
        onSelectTabProp={tabNumber => {
          setPaymentTab(tabNumber)
        }}
        label={'Payment method'}
        title1={'Bank account'}
        title2={'Credit card'}
        title3={payrollDeductionTitle}
        isPayrollDeductionAvailable={isPayrollDeductionAvailable}
        inputFromProps={
          checkFetchData(PAYMENT_METHOD_SELECTION) ?
            getPrefetchData[PAYMENT_METHOD_SELECTION]?.value : null
        }
        visibility={true}
      />
      {paymentTab === 0 &&
        <BankAccountTab
          onBankAccountTabValidated={wasSuccessful => {
            setBankAccountPaymentValidation(wasSuccessful)
          }}
          onBankAccountData={data => {
            setBankAccountTabData(data)
          }}
          inputFromProps={checkFetchData(BANKACCOUNT_PAYMENT_TAB_DATA) ?
            getPrefetchData[BANKACCOUNT_PAYMENT_TAB_DATA][BANKACCOUNT_AGREE_TERM_AND_CONDITION]?.value : undefined}
          isValidated={checkFetchData(BANKACCOUNT_PAYMENT_TAB_DATA) ?
            getPrefetchData[BANKACCOUNT_PAYMENT_TAB_DATA][BANKACCOUNT_AGREE_TERM_AND_CONDITION]?.isValidated : false}
        />
      }

      {paymentTab === 1 &&
        <CreditCardPaymentTab
          onCreditCardPaymentTabValidated={wasSuccessful => {
            setCreditCardPaymentValidation(wasSuccessful)
          }}
          onCreditCardPaymentData={data => {
            setCreditCardTabData(data)
          }}
          inputFromProps={
            getPrefetchData[CREDITCARD_PAYMENT_TAB_DATA][CREDITCARD_PAYMENT_AGREE_TERM_AND_CONDITION]?.value}
          isValidated={
            getPrefetchData[CREDITCARD_PAYMENT_TAB_DATA][CREDITCARD_PAYMENT_AGREE_TERM_AND_CONDITION]?.isValidated}
        />
      }

      {paymentTab === 2 &&
        <PayDeductionTab
          expandableParagraphIsLongTextProp={true}
          expandableParagraphDescriptionTextProp={'I HEREBY apply for membership of the Shop, Distributive and Allied Employees Association, South Australian Branch as registered under the Fair Work (Registered Organisations) Act 2009 (Cth) and (if eligible) the Shop, Distributive and Allied Employees Association, South Australian Branch as registered under the Fair Work Act 1994 (SA). I agree to comply with and be bound by the Rules of each Association (SDA) as ammended from time to time. I acknowledge the SDA is affiliated to the Australian Council of Trade Unions, the Australian Labor Party and Union Network International. I authorise the SDA to use my personal information in order to communicate with me, for direct marketing, research and member surveys. Privacy Act 1998 - The SDA is bound by the Privacy Act. This information is collected to enable the SDA to contact you about matters relating to your SDA membership, and to ensure that we have the necessary information to represent your employment and related interests. A copy of the SDAs Privacy Statement is available from the SDA office and our website'}
          setPayDeductionTabValidation={wasSuccessful => {
            setPayDeductionPaymentValidation(wasSuccessful)
          }}
          onPayDeductionTabData={data => {
            setDeductionTabData(data)
          }}
          inputFromProps={
            getPrefetchData[PAY_DEDUCTION_PAYMENT_TAB_DATA][PAY_DEDUCTION_PAYMENT_ELOTRONIC_SIGNATURE]?.value
          }
        />
      }
      <ProgressButtonPositioningDiv>
        <ProgressButton
          widthSize="13.875rem"
          title={progressButtonTilte}
          buttonStates={proceedingButtonState}
          onClickCallbackProp={() => {
            setProceedingButtonState(ButtonStates.progressing)
            setProgressButtonTilte('Processing')
          }}
          onClickExecFunc={() => {
            return "SUCCESS"
          }}
          onSuccessfulCallback={(data: any) => {
            if (creditCardPaymentValidation === true || bankAccountPaymentValidation === true) {
              return 'SUCCESS'
            }
          }}
          onFailCallback={(error: any) => {
            // if (payDeductionPaymentValidation === true) {
            //   return 'Error'
            // }
          }}
        />
      </ProgressButtonPositioningDiv>
    </FormStep4Wrapper>
  )
}

export default FormStepFour
